
.lumos-tabbar {
  bottom: 0;
  left: 0;
  width: 100%;
  height: 8rem;
  z-index: 10;
  background-color: #fff;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  margin: 0;
  padding: 0;
  -webkit-box-shadow: 0 0 10px rgba(0, 0, 0, 0.15);
          box-shadow: 0 0 10px rgba(0, 0, 0, 0.15);
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.lumos-tabbar.fixed {
  position: fixed;
}
.lumos-tabbar .lumos-tab-item {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  position: relative;
}
.lumos-tabbar .lumos-tab-icon {
  -webkit-box-flex: 0;
      -ms-flex: none;
          flex: none;
  height: 32px;
  width: 32px;
  text-align: center;
  margin: auto;
}
.lumos-tabbar .lumos-tab-icon img {
  display: block;
  width: 100%;
  height: 100%;
}
.lumos-tabbar .lumos-tab-text {
  font-size: 32px;
}
.lumos-tabbar .lumos-tab-vonbadge {
  position: absolute;
